interface Person {
  name: string;
  age: number;
  gender: string;
}

class Teacher {
  constructor(private info: Person) {}
  getInfo(key: string) {
    return this.info[key];
  }
}

const teacher = new Teacher({
  name: "FattyCat",
  age: 18,
  gender: "male",
});

const test = teacher.getInfo("name");
console.log(test);
